const CYRILIC_LETTERS =
  "                                                           ".split(
    " "
  );
const LATIN_LETTERS =
  "a b v g d  e  z i j k l lj m n nj o p r s t  u f h c  d  A B V G D  E  Z I J K L Lj M N Nj O P R S T  U F H C  D ".split(
    " "
  );

export const convertCyrilicToLatin = (string: string): string => {
  return string
    .split("")
    .map((char: string): string => {
      const characterIndex = CYRILIC_LETTERS.indexOf(char);
      if (!~characterIndex) {
        return char;
      }

      return LATIN_LETTERS[characterIndex];
    })
    .join("");
};

export const convertLatinToCyrilic = (string: string): string => {
  return string
    .split("")
    .map((char: string): string => {
      const characterIndex = LATIN_LETTERS.indexOf(char);
      if (!~characterIndex) {
        return char;
      }

      return CYRILIC_LETTERS[characterIndex];
    })
    .join("");
};
